}

.color-dark {
  color: #301566;
}

h1 {
  font-family: "Poppins", sans-serif;
  font-size: 80px;
  font-weight: 600;
  line-height: 40px;
  margin-bottom: 20px;
}

h2 {
  font-family: "Poppins", sans-serif;
