  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  margin-bottom: 50px;
}

hr {
  border-top: 2px solid #301566;
  margin: 10px 0 30px 0;
}

a.button {
  padding: 20px 40px;
  background-color: #00A015;
  margin: 0 auto;
  border-radius: 50vh;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 15px;
  border: 2px solid #00A015;
}
a.button:hover {
  opacity: 0.9;
}
a.button.to-gray:hover {
  border: solid white 2px;
  background-color: transparent;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  padding: 20px 20px;
  height: 155px;
}
header .container {
  max-width: 1170px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}
header .container .logo {
  display: flex;
}
header .container nav {
  height: 115px;
  width: 600px;
  display: flex;
  align-items: center;
  justify-content: right;
}
header .container nav ul {
  display: flex;
}
header .container nav ul li {
  margin: 0 10px;
  list-style: none;
}
header .container nav ul li a {
  position: relative;
  font-size: 16px;
  font-weight: 500;
  color: #301566;
}
header .container nav ul li a::after {
  display: block;
  position: absolute;
  content: "";
  width: 0;
  height: 2px;
  top: -2px;
  left: 0;
  right: 0;
  background-color: #00A015;
  transition: all 0.3s ease;
  margin: 0 auto;
  opacity: 0;
}
header .container nav ul li a:hover::after {
  width: 100%;
  opacity: 1;
}
header .container nav ul li a:hover {
  opacity: 0.9;
  color: #00A015;
}
header .container nav ul li a.active {
  color: #00A015;
}
header .container nav ul li .sub-menu {
  z-index: 2;
  right: 0;
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: white;
  box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;
  min-width: max-content;
  visibility: hidden;
  opacity: 0;
}
header .container nav ul li .sub-menu li {
  padding: 10px 20px;
}
header .container nav ul li.parent-sub-menu {
  position: relative;
}
header .container nav ul li.parent-sub-menu a svg {
  height: 10px;
  padding-bottom: 2px;
  padding-left: 5px;
}
header .container nav ul li.parent-sub-menu:hover .sub-menu {
  visibility: visible;
  opacity: 1;
}
header .container nav ul li.parent-sub-menu:hover svg {
  fill: #00A015;
}
header .container nav ul li.parent-sub-menu:hover > a {
  color: #00A015;
}

